	module.exports = {"data":{"site":{"siteMetadata":{"title":"Blog by Michael Lancaster","subtitle":"Software Engineer focused in Javascript applications","copyright":"© All rights reserved.","author":{"name":"Michael Lancaster","twitter":"weblancaster"},"url":"http://www.bymichaellancaster.com"}},"markdownRemark":{"id":"/Users/michaellancaster/Sites/github/michaellancaster-blog/src/pages/articles/2013-02-27-the-filter-bubble-and-your-social-life-online/index.md absPath of file >>> MarkdownRemark","html":"<p>At the same time Internet give us liberty to think and see different points about our world in culture, politics, nature, life in general the internet is made by “man” and we have our own perspective of the world.\nTherefore we are doing the same exactly mistakes we are doing offline it’s just another digital space 1 and 0’s.</p>\n<p>It’s sort of funny sometimes how we push things turning every person the “product of the environment” even on digital.</p>\n<p>I’ll never deny that technology in particular internet changed the world for better but I still think we could do much better when we start to change our mindset as a “person” for the community.</p>\n<p>Watching this great video we can see that people don’t loose their old habits to live in their “boxes” and continue to create their “Extended Mind”</p>\n<h2>Are we going backwards?!</h2>\n<iframe src=\"https://www.youtube.com/embed/B8ofWFx525s\" frameborder=\"0\" allowfullscreen=\"allowfullscreen\"> </iframe>\n<h2>I also suggest..</h2>\n<p>you to watch this incredible documentary called <a href=\"http://internetrising.net/\">Internet Rising</a> investigating the evolving relationships between the Internet and collective consciousness of humanity. It provokes many questions about ancient and modern paradoxes of life. You definitely should check it out.</p>\n<iframe src=\"https://www.youtube.com/embed/pMh8oBdKkK4\" frameborder=\"0\" allowfullscreen=\"allowfullscreen\"> </iframe>\n<p>Both videos are great to inspired us to think about almost everything surround us in the digital life.</p>","fields":{"date":"2013-02-27"},"frontmatter":{"title":"The filter bubble and your social life online.","description":"At the same time Internet give us liberty to learn and see different things about our world, about cultures, politics, nature, life in general..."}}},"pathContext":{"slug":"/articles/2013-02-27-the-filter-bubble-and-your-social-life-online/"}}

